.nt-card-content__media-live-label {
	background: #ff2c33;
	display: inline-block;
	border-radius: 2px;
	text-transform: uppercase;
	position: absolute;
	top: 2px;
	left: 2px;
	z-index: 2
}
.nt-card-settings-dropdown__tooltip {
	margin-left: 5px 0 0 5px
}
.nt-card-settings-dropdown__content {
	margin-right: -16px;
	padding: 0;
	width: max-content
}
.nt-card-settings-dropdown__item {
	border-bottom: 1px solid #cdcfd2;
	width: 100%
}
.nt-card-settings-dropdown__item:last-child {
	border-bottom: none
}
.nt-card-settings-dropdown__trigger-icon {
	color: rgba(0, 0, 0, .6)
}
.nt-card-settings-dropdown__icon {
	vertical-align: middle;
	min-width: 24px
}
.nt-social-counts__count:nth-child(n+2)::before {
	margin: 0 4px;
	content: "•"
}
.nt-social-counts__trending-icon {
	color: #2f7b15;
	margin-right: 8px
}
.nt-notif-pill-container {
	display: flex;
	justify-content: center;
	position: fixed;
	z-index: 100
}
.nt-notif-pill-container__pill {
	background-color: #0073b1;
	border-radius: 32px;
	font-weight: 600;
	padding: 8px 16px
}
.nt-card__main {
	border-radius: 0;
	margin: 0;
	padding: 16px 16px 16px 12px
}
.nt-card__main:first-child {
	border-radius: 2px 2px 0 0
}
.nt-card__main:last-child {
	border-radius: 0 0 2px 2px
}
.nt-card__left-rail {
	width: 56px;
	align-items: center
}
.nt-card__core-rail {
	margin-right: 12px
}
.nt-card__core-rail,
_:-ms-lang(x) {
	max-width: 420px
}
.nt-card__right-rail {
	flex-direction: column
}
.nt-card__time-ago {
	justify-content: right;
	white-space: nowrap
}
.nt-card-content__img {
	height: 76px;
	width: 145px
}
@-moz-document url-prefix() {
	.nt-card__core-rail {
		max-width: 420px
	}
	.nt-card-content__body-text {
		width: inherit
	}
	.nt-card-content__body-text--image {
		max-width: 279px
	}
	.nt-card-content__body-text--entity {
		max-width: 364px
	}
}
.nt-card-content__body-text--image,
_:-ms-lang(x) {
	max-width: 259px
}
.nt-card-content__body-text--entity,
_:-ms-lang(x) {
	max-width: 344px
}
.nt-card-settings-dropdown__item {
	padding: 4px 16px
}
.nt-card-settings-dropdown__item:hover {
	background-color: #f3f6f8
}
.nt-card-settings-dropdown__trigger {
	cursor: pointer
}
.nt-social-counts {
	margin-top: 12px
}
.nt-viral-panel__carousel-wrapper {
	width: 100%
}
.nt-viral-panel__carousel-wrapper .artdeco-carousel-slide-container {
	min-width: 177px
}
.nt-viral-panel__carousel-wrapper .artdeco-carousel-container .pagination-btns {
	padding: 5px 0;
	margin: 0
}
.nt-viral-panel__carousel-wrapper .artdeco-carousel-container .pagination-btns span {
	font-size: 14px
}
.nt-viral-panel__carousel-wrapper .artdeco-carousel-slide {
	width: 165px;
	height: 196px;
	margin-top: 2px;
	margin-left: 2px
}
.nt-viral-panel__see-all-wrapper {
	position: relative
}
.nt-viral-panel__see-all-wrapper .artdeco-carousel-container .pagination-btns span {
	display: none
}
.nt-viral-panel__see-all {
	position: absolute;
	right: 84px
}
.nt-segment-header {
	border-bottom: 1px solid #cfcfcf;
	padding: 8px 12px;
}
.collapsable-sticky-header.collapsable-sticky-header {
	height: 48px;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
	pointer-events: none
}
.collapsable-sticky-header.collapsable-sticky-header.show-header {
	pointer-events: auto
}
.header-container {
	position: fixed;
	top: 44px;
	width: 100%;
	z-index: 100
}
.nav-hidden .header-container {
	top: 0
}
.header-container .header-content {
	background-color: #0073b1;
	position: relative;
	transition: transform .5s;
	top: -48px
}
.show-header .header-container .header-content {
	transform: translate3d(0, 48px, 0)
}
.ember-view svg {
  margin-right: 5px;
}