.type-ahead-wrapper {
	position: relative
}
.type-ahead-results {
	position: absolute;
	width: 100%;
	max-height: 192px;
	border: 1px solid #647c8d;
	border-radius: 2px;
	box-shadow: 0 2px 4px rgba(0, 0, 0, .25);
	overflow: auto
}
.type-ahead-results:empty {
	border: none;
	overflow: hidden
}
.type-ahead-result.is-active {
	background-color: #e6e9ec
}
.type-ahead-result:first-child {
	border-radius: 2px 2px 0 0
}
.type-ahead-result:last-child {
	border-radius: 0 0 2px 2px
}
.type-ahead-result-image {
	margin: 2px 8px
}
.type-ahead-result-info h3 {
	font-weight: 400
}
.type-ahead-entity h3 {
	font-size: 16px
}
.search-typeahead-v2 .typeahead-suggestion {
	padding: 0;
	list-style: none
}
.search-typeahead-v2 .basic-typeahead__triggered-content {
	position: absolute;
	left: 0;
	overflow-y: auto;
	border: 1px solid #cdcfd2;
	background-color: #fff
}
.search-typeahead-v2 .basic-typeahead__triggered-content:empty {
	border: none
}
.search-typeahead-v2 .basic-typeahead__selectable {
	padding: 2px
}
.search-typeahead-v2 .basic-typeahead__selectable.basic-typeahead__selectable--active {
	padding: 0;
	border: 2px solid #0073b1
}
.basic-typeahead__selectable--hover,
.basic-typeahead__selectable--hover .search-typeahead-v2__hit {
	background-color: #e6e9ec
}
.ivm-view-attr__img-wrapper--use-img-tag .search-typeahead-v2__image-scale.search-typeahead-v2__image-scale {
	object-fit: scale-down
}
.search-typeahead-v2__section-header--top-divider {
	border-top: 1px solid rgba(0, 0, 0, .15)
}
.search-typeahead-v2__section-header--bottom-divider {
	border-bottom: 1px solid rgba(0, 0, 0, .15)
}
.search-typeahead-v2__icon--entry-point {
	color: #0073b1
}
.search-typeahead-v2__hit {
	cursor: pointer;
	display: flex;
	align-items: center;
	padding: 2px 8px 4px;
	box-sizing: border-box
}
.typeahead-suggestion:first-of-type .search-typeahead-v2__hit {
	padding-top: 4px
}
.search-s-facet .search-typeahead-v2__hit {
	padding-top: 8px;
	padding-bottom: 8px
}
.search-quelp-typeahead-v2 .search-typeahead-v2__hit {
	color: rgba(0, 0, 0, .9);
	padding: 2px 8px
}
.search-typeahead-v2__hit--autocomplete {
	padding: 8px 4px 4px
}
.search-typeahead-v2__hit--non-profile-entity .EntityPhoto-square-2:not([class*=ghost-]) {
	background-color: #fff
}
.search-typeahead-v2__hit--escape-hatch {
	border-top: 2px solid #e1e9ee;
	padding: 12px 8px
}
.search-typeahead-v2__clear-history-item {
	position: absolute;
	top: 12px;
	right: 8px
}
.search-typeahead-v2__clear-history-item:hover {
	background-color: transparent
}
.search-typeahead-v2__clear-history-button.search-typeahead-v2__clear-history-button--focused.artdeco-button {
	background-color: #e6e9ec;
	color: rgba(0, 0, 0, .75)
}
.search-typeahead-v2__illustration--hashtag {
	background-image: url(/sc/h/a7fktfypzh6zhydqwuu644apv);
	background-position: 50%;
	width: 40px;
	height: 40px
}
.search-typeahead-v2__hit--non-profile-entity .search-typeahead-v2__image {
	width: 40px;
	height: 40px;
	box-sizing: border-box;
	background-clip: content-box;
	border: 4px solid transparent;
	border-radius: 6px;
	margin: 0
}
.search-typeahead-v2__hit--profile-entity .search-typeahead-v2__image {
	width: 40px;
	height: 40px;
	box-sizing: border-box;
	background-clip: content-box;
	border: 3px solid transparent;
	border-radius: 49.9%;
	margin: 0
}
.search-typeahead-v2__hit-info {
	line-height: 1.42857;
	padding-left: 8px
}
.search-quelp-typeahead-v2 .search-typeahead-v2__hit-info {
	padding-left: 0
}
.search-typeahead-v2__hit--escape-hatch .search-typeahead-v2__hit-info {
	margin: auto
}
.search-typeahead-v2__hit--autocomplete .search-typeahead-v2__hit-text,
.search-typeahead-v2__hit--entry-point .search-typeahead-v2__hit-text {
	font-weight: 400
}
.search-typeahead-v2__hit--escape-hatch .search-typeahead-v2__hit-text {
	color: #0073b1
}
.search-s-facet .search-typeahead-v2__hit-subtext {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	display: block;
	width: 100%
}
.search-typeahead-v2__history-list-carousel {
	overflow-y: auto;
	white-space: nowrap
}
.search-typeahead-v2__entity-hit {
	cursor: pointer;
	height: 80px;
	width: 80px
}
.search-global-typeahead--resize-restyled .search-typeahead-v2__entity-hit {
	width: 70px
}
.typeahead-suggestion--carousel-item-text {
	/*! autoprefixer: off */
	display: block;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	margin: 0 auto;
	width: 100%;
	white-space: normal;
	word-break: break-all;
	word-break: break-word;
	-webkit-hyphens: auto;
	-moz-hyphens: auto;
	hyphens: auto
}
.search-typeahead-v2__quelp-entry-point.search-typeahead-v2__quelp-entry-point {
	padding: 12px 4px
}
.search-typeahead-v2__quelp-entry-point.search-typeahead-v2__quelp-entry-point.basic-typeahead__selectable--active {
	padding: 10px 2px;
	border: 2px solid #0073b1
}
.grid {
	box-sizing: border-box;
	display: flex;
	flex-wrap: wrap;
	margin-left: auto;
	margin-right: auto;
	width: 100%
}
